.content-home {
    h1 {
        font-size: 6rem;
        font-family: 'Bebas Neue';
        letter-spacing: 0.5rem;
        line-height: 6rem;
        margin-bottom: 5px;

        @media (max-width: 768px) { 
            line-height: 5.3rem;
            font-size: 5rem;
            opacity: 0;
        }
        
    }

    h2 {
        font-family: 'Helvetica';
        src: url('../../../assets/helvetica-light.ttf');

        @media (max-width: 768px) { 
            font-size: 2rem;
            opacity: 0;
        }
    }

    h3 {
        font-size: 1rem;
        padding-bottom: 2rem;

        @media (max-width: 768px) { 
            opacity: 0;
        }
    }

    .content-buttons {
        @media (max-width: 768px) { 
            opacity: 0;
        }
    }

    @media (max-width: 768px) {
        width: 75%;
    }

    @media (max-width: 434px) {
        h1{
            font-size: 4rem;
            letter-spacing: 0.3rem;
            line-height: 4rem;
        }
        h2 {
            font-size: 1.5rem;
        }
        h3 {
            font-size: .8rem;
        }
        width: 80%;
    }
}