.background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;

    &-container {
        position: relative;
        display: flex;
        width: 100%;
        height: 100%;
    }

    &-column-1{
        background: #ffffff;
        width: 50%;
        height: 100%;
        position: absolute;
        z-index: 1;
        -webkit-box-shadow: 10px 0 20px 2px #969696;
        box-shadow: 10px 0 20px 2px #969696;
        left: -50%;
        transition: all .8s ease-out;

        @media (max-width: 768px) { 
            width: 75%;
            left: -75%;
        }
    }

    &-column-2{
        filter: brightness(95%);
        background: url('../../assets/background.png') no-repeat bottom;
        background-size: cover;
        width: 100%;
        height: 100%;
        position: absolute;
        
    }

    &-block-1 {
        background: #000;
        width: 15rem;
        height: 4rem;

        position: absolute;
        right: -100%;
        transition: all ease-out 1s;
        z-index: 2;

        @media (max-width: 768px) { 
            width: 10rem;
            height: 2rem;
        }
    }

    &-block-2 {
        background: #000;
        width: 15rem;
        height: 4rem;

        position: absolute;
        left: -100%;
        bottom: 0;
        transition: all ease-out 1s;
        z-index: 2;

        @media (max-width: 768px) { 
            width: 10rem;
            height: 2rem;
        }
    }
}