.left-content {
    h1 {
        font-size: 6rem;
        font-family: 'Bebas Neue';
        letter-spacing: 0.5rem;
        line-height: 6rem;
        margin-bottom: 5px;

        @media (max-width: 768px) { 
            line-height: 5.3rem;
            font-size: 5rem;
        }
        
    }

    h2 {
        @media (max-width: 768px) { 
            font-size: 2rem;
        }
    }

    h3 {
        font-size: 1rem;
        padding-bottom: 2rem;
    }

    p {
        transition: ease-out all 1s;
        opacity: 0;
    }
    
    .content-buttons {
        transition: ease-out all 1s;
        opacity: 0;
    }

    @media (max-width: 768px) {
        width: 75%;
    }
    
}

.right-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    .bio-photo {
        width: 70%;
        border-radius: 50%;
        -webkit-box-shadow: 0 0 20px 10px #969696;
        box-shadow: 0 0 20px 10px #969696;
        margin-bottom: 2rem;
        opacity: 0;
        transition: all 1s ease-out;
    }

    h4 {
        font-family: 'Bebas Neue';
        font-weight: bold;
        font-size: 2.5rem;
        letter-spacing: 0.15rem;
        margin-bottom: 1rem;
        opacity: 0;
        transition: all 1s ease-out;

    }

    p {
        text-align: justify;
        font-weight: 600;
        margin-bottom: 1rem;
        opacity: 0;
        transition: all 1s ease-out;

    }

    button {
        opacity: 0;
        transition: all 1s ease-out;

    }

    @media (max-width: 434px) {
        h4 {
            width: 100%;
            text-align: center;
        }
        .bio-photo {
            width: 50%;
            -webkit-box-shadow: 0 0 10px 5px #969696;
            box-shadow: 0 0 10px 5px #969696;
        }
        p {
            font-size: .8rem;
        }
    }
}