body {
    overflow: hidden;
    font-family: 'Helvetica';
    src: url('./assets/helvetica-light.ttf');
}

button {
    padding: 10px 20px;
    margin-right: 10px;
    font-weight: 600;
    transition: all .2s ease-out;
    margin-bottom: 10px;

    background-color: #000;
    border: 2px solid #000;
    color: #fff;

    &:hover {
        background-color: #fff;
        border: 2px solid #000;
        color: #000;
    }
}

@media (max-width: 768px) { 
    .hidden-sm {
        display: none;
    }
}

@media (min-width: 769px) { 
    .show-sm {
        display: none;
    }
}
